import { createStore } from "vuex";
import axios from "axios"
import router from "../router"

export default createStore({
  state: {
    dataFull : {
        tarih : null, 
        kbara : null, 
        tbara : null, 
        hadde : null, 
        vpsa : null, 
        aba2 : null, 
        aba2Dun : null, 
        ayT : null, 
        ayY : null, 
        dun : null, 
        gun : null ,
        // csrfToken : null,
    },
    online: 0,
    socketList:[],
    socketIPList:[],
    socketTimeList:[],
    logData:[],
    userName: null,
    isAdmin: false,
    //isAuthenticated : false,
    token : "",
  },
  mutations: {
    updateData(state, data) {
        console.log("mutations çalıştı")
        state.dataFull = data
    },
    updateLogData(state, data){
      state.logData = data
    },
    updateUserData(state, data){
      state.userName = data.name
      state.token = data.token
      //state.isAuthenticated = true;
    }
  },
  getters: {
    getData(state) {
        // console.log("data: " + state.dataFull.length)
        console.log("getters çalıştı")
        return state.dataFull;
    },
    getLogData(state) {
      return state.logData;
    },
    isAuthenticated(state){
      return state.token !== ""
  }
  },
  actions : {
    initAuth({ state, dispatch}){
      console.log("initAuth çalıştı.")
      let token = localStorage.getItem("token")
      console.log(token)
       if(token){
          console.log("if olumlu")
          //state.token = token
           let expirationDate = localStorage.getItem("expirationDate")
           let time = new Date().getTime()

           if(time >= +expirationDate){
               console.log("token süresi geçmiş...")
               dispatch("logout")
           } else {
               state.token = token
               let timerSecond = +expirationDate - time
               console.log(timerSecond)
               dispatch("setTimeoutTimer", timerSecond)
               router.push("/socket")
            }

       } else {
           router.push("/auth")
           return false
       }
   },
    initApp({commit}) {
        // Vue Resource İşlemleri...
        console.log("actions çalıştı")
        axios({
            url: "http://3.120.192.166/rapor",
            method: "GET",
          }).then(response => {
            console.log(response.data)

            let data = response.data;
            commit("updateData", data);

            // for (let key in data) {
            //     console.log(key)
            //     // commit("updateData", data[key]);
            // }
          });
        //   return state.kbara
    },
    conLog({state, commit}){
        //const headers = { "Content-Type": "application/json", "Authorization" : state.token };
        // axios({
        //     url: "http://localhost/log",
        //     method: "GET",
        //     "Content-Type": "application/json",
        //     "Authorization" : state.token
        //   })
        const headers = { "Content-Type": "application/json", "Authorization" : state.token };
        axios.get("http://3.120.192.166/log", { headers })
          .then(response => {
            let data = response.data;
            commit("updateLogData", data);
          });
    },
    login({commit,dispatch},authData) {
      console.log(authData)
      //console.log(password)
      axios.post('http://3.120.192.166/login', {
          email: authData.email,
          password: authData.password,
          // _csrf : state.dataFull.csrfToken
      }).then(response => {
        commit("updateUserData", response.data);
        localStorage.setItem("token", response.data.token)
        localStorage.setItem("expirationDate", new Date().getTime() + +response.data.expiresIn * 1000)

        dispatch("setTimeoutTimer", +response.data.expiresIn * 1000)
        router.replace("/socket") 
        console.log(response.data)
      });
      
    },
    logout({state}){
        //commit("clearToken")
        state.token = ""
        localStorage.removeItem("token")
        localStorage.removeItem("expirationDate")
        router.replace("/auth")
    },
    setTimeoutTimer({dispatch}, expiresIn){
        setTimeout(() => {
            dispatch("logout")
        }, expiresIn)
    }
  },
  plugins: [
    
  ]
});
